import { EdaService } from './../../eda/eda.service';
import { DataDriftService } from './../data-drift.service';
import { DataService } from './../../data/data.service';
import { LocalStorageService } from './../../../shared/local-storage.service';
import { SpinnerService } from './../../../shared/spinner.service';
import { Component, Input, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { ToastrService } from 'ngx-toastr';
import { SocketIoService } from 'src/app/core/socket-io.service';

const Plotly = require('plotly.js-dist');

@Component({
  selector: 'app-drift-overview',
  templateUrl: './drift-overview.component.html',
  styleUrls: ['./drift-overview.component.scss'],
})
export class DriftOverviewComponent implements OnInit {
  @Input() reportDetails: any;

  /** Form control */
  inputFeature = new FormControl('', Validators.required);

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  /** Contant arrays */
  psiThresholdArray: Array<any> = [
    { name: 'Less than 0.1 (No change in population)', value: '<0.1' },
    { name: '0.1 – 0.20 (Some minor change in population)', value: '0.1-0.20' },
    { name: 'Greater than 0.20 (Significant change in population)', value: '>0.20' },
  ];
  stdThresholdArray: Array<any> = [
    { name: '1 STD (covers 68% of data)', value: '1' },
    { name: '2 STD (covers 95% of data)', value: '2' },
    { name: '3 STD (covers 99.7% of data)', value: '3' },
  ];

  project: any;
  baselineStats: Array<any>;
  incomingStats: Array<any>;
  featureWiseDistributionPlot: any;
  modelInfo: Array<any> = [];

  constructor(
    private _spinner: SpinnerService,
    private _localStorage: LocalStorageService,
    private _data: DataService,
    private _toaster: ToastrService,
    private _dataDrift: DataDriftService,
    private _socket: SocketIoService,
    private _eda: EdaService
  ) {}

  /**
   * Initialize the module with required data
   */
  ngOnInit(): void {
    this._spinner.showSpinner();
    this.initSocket();
    this.project = this._localStorage.getItem('project');
    this.reportDetails.incomingFileId.filename = this.reportDetails.incomingFileId.filename.substr(
      0,
      this.reportDetails.incomingFileId.filename.lastIndexOf('.')
    );
    this.modelInfo = [
      {
        text: 'Incoming Dataset Name',
        data: this.reportDetails.incomingFileId.filename,
        cols: 5,
        rows: 1,
        // color: '#e6eef5',
        color: '#cddceb',
        icon: 'fas fa-database fa-3x mr-3 mb-2',
      },
      {
        text: 'Model Name',
        data: this.reportDetails.modelId.modelMetaData.algoName,
        cols: 2,
        rows: 1,
        color: '#ffdab4',
        icon: 'fas fa-cubes fa-3x mr-3 mb-2',
      },
      {
        text: 'Target Name',
        data: this.reportDetails.depVariable,
        cols: 1,
        rows: 1,
        color: '#ffdab4',
        icon: 'fas fa-bullseye fa-3x mr-3 mb-2',
      },
      {
        text: 'No. of Rows',
        data: this.reportDetails.incomingFileId.noOfRows,
        cols: 1,
        rows: 1,
        color: '#ffdab4',
        icon: 'fas fa-info fa-3x mr-3 mb-2',
      },
      {
        text: 'No. of Cols',
        data: this.reportDetails.incomingFileId.noOfCols,
        cols: 1,
        rows: 1,
        color: '#ffdab4',
        icon: 'fas fa-info fa-3x mr-3 mb-2',
      },
    ];
  }

  /**
   * Plots all the graphs using Plotly.js after view is rendered
   */
  ngAfterViewInit() {
    // Distribution Plot
    if (this.reportDetails?.conceptDrift?.distributionPlot) {
      this.reportDetails.conceptDrift.distributionPlot.layout.template.layout.plot_bgcolor =
        'white';
      this.reportDetails.conceptDrift.distributionPlot.layout.xaxis.showgrid = false;
      this.reportDetails.conceptDrift.distributionPlot.layout.yaxis.showgrid = false;
      this.reportDetails.conceptDrift.distributionPlot.layout.yaxis.showline = true;
      this.reportDetails.conceptDrift.distributionPlot.layout.yaxis.showline = true;
      this.reportDetails.conceptDrift.distributionPlot.layout.xaxis.linecolor = 'black';
      this.reportDetails.conceptDrift.distributionPlot.layout.yaxis.linecolor = 'black';
      Plotly.plot(
        document.getElementById('distributionPlot'),
        this.reportDetails.conceptDrift.distributionPlot.data,
        this.reportDetails.conceptDrift.distributionPlot.layout
      );
    }

    // Model Performance Plot
    if (this.reportDetails?.conceptDrift?.performancePlot) {
      this.reportDetails.conceptDrift.performancePlot.layout.template.layout.plot_bgcolor = 'white';
      (this.reportDetails.conceptDrift.performancePlot.layout.xaxis.showgrid = false),
        (this.reportDetails.conceptDrift.performancePlot.layout.yaxis.showgrid = false),
        (this.reportDetails.conceptDrift.performancePlot.layout.yaxis.showline = true),
        (this.reportDetails.conceptDrift.performancePlot.layout.yaxis.showline = true),
        (this.reportDetails.conceptDrift.performancePlot.layout.xaxis.linecolor = 'black'),
        (this.reportDetails.conceptDrift.performancePlot.layout.yaxis.linecolor = 'black');
      Plotly.plot(
        document.getElementById('modelPerformancePlot'),
        this.reportDetails.conceptDrift.performancePlot.data,
        this.reportDetails.conceptDrift.performancePlot.layout
      );
    }
    this._spinner.stopSpinner();
  }

  /**
   * Fetches all the required data and creates API parsing object structure
   * @param inputFeature selected feature
   */
  async featurewiseChanged(inputFeature: string): Promise<any> {
    this._spinner.showSpinner();
    // Request parsing object
    let reqObj: any = {
      algoType: this.reportDetails.algoType,
      type: 'feature_wise',
      stdThreshold: this.project.stdThreshold || 3,
      reportName: this.reportDetails.reportName,
      inputFeature: inputFeature,
      incomingFileId: this.reportDetails.incomingFileId,
    };
    // API call
    let dataSet: any = await this._data
      .getAllProjectDatas(this.project._id)
      .pipe(takeUntil(this.unsubscribe$))
      .toPromise()
      .catch((err: Error) => {
        this._spinner.stopSpinner();
        this._toaster.error('Cannot generate featurewise drift report', 'Drift Reports');
      });
    let selectedDataSet: any = dataSet[0].filesList.find(
      (item: any) => item._id === this.reportDetails.incomingFileId._id
    );
    // API call
    let originalFileWithEdaDetails: any = await this._data
      .getAllChildDatasets(this.project._id, selectedDataSet._id)
      .pipe(takeUntil(this.unsubscribe$))
      .toPromise()
      .catch((err: Error) => {
        this._spinner.stopSpinner();
        this._toaster.error('Cannot generate featurewise drift report', 'Drift Reports');
      });
    if (originalFileWithEdaDetails[0].currentEdaId) {
      reqObj.edaId = originalFileWithEdaDetails[0].currentEdaId;
    } else {
      reqObj.edaId = this.reportDetails.edaId;
      reqObj.filename = this.reportDetails.incomingFileId.filename;
    }
    reqObj.incomingFilepath = selectedDataSet.name;
    reqObj.incomingSchema = selectedDataSet.fileSchema.map((item: any) => {
      let obj = { colName: item.colName, dataType: item.dataType };
      return obj;
    });
    this.getFeaturewiseReport(reqObj);
  }

  /**
   * Generates a featurewise drift report by API call
   * @param reqObj
   */
  getFeaturewiseReport(reqObj: any): void {
    this._dataDrift
      .startDriftAnalysis(this.reportDetails.trainingId._id, this.reportDetails.modelId._id, reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this._toaster.info('Drift analysis started', 'Drift Reports');
          } else {
            this._spinner.stopSpinner();
            this._toaster.error('Cannot generate featurewise drift report', 'Drift Reports');
          }
        },
        (err: Error) => {
          this._spinner.stopSpinner();
          this._toaster.error('Server Error', 'Drift Settings');
        }
      );
  }

  /**
   * Converts the value into full message
   * @param {*} threshodType values
   */
  convertToMessage(threshodType: string): string {
    let msg: string;
    if (threshodType === '>0.20' || threshodType === '<0.1' || threshodType === '0.1-0.20') {
      this.psiThresholdArray.filter((item) =>
        item.value === threshodType ? (msg = item.name) : ''
      );
    } else {
      this.stdThresholdArray.filter((item) =>
        item.value === threshodType.toString() ? (msg = item.name) : ''
      );
    }
    return msg;
  }

  /**
   * Converts the message as per the type of reponse
   * @param {*} item data
   */
  dataFiltering(item: any): string {
    let val: string;
    if (item.hasDrifted === 0 || item.hasDrifted === 'false') {
      val = 'false';
    } else if (item.hasDrifted === 1 || item.hasDrifted === 'true') {
      val = 'true';
    } else {
      val = '-';
    }
    return val;
  }

  /**
   * Converts the message as per the type of reponse
   * @param {*} item data
   */
  metricValue(item: any): string {
    var val = '';
    if (item.message == null) {
      val = item.metricValue;
    } else {
      val = '-';
    }
    return val;
  }

  /**
   * Initializes the socket to listen to the events
   */
  initSocket(): void {
    this._socket.listen('dataDrift').subscribe(
      (res) => {
        let graphDiv: any = document.getElementById('featureWiseDistribution');
        while (graphDiv.data && graphDiv.data.length > 0) {
          Plotly.deleteTraces(graphDiv, [0]);
        }
        if (res.status == 'datadrift_failed') {
          this._toaster.error(res.data, 'Drift Report');
        } else {
          let bstats = res.data.baselineStats;
          let istats = res.data.incomingStats;
          for (var propName in bstats) {
            if (bstats[propName] === '') {
              delete bstats[propName];
            }
          }
          for (var propName in istats) {
            if (istats[propName] === '') {
              delete istats[propName];
            }
          }
          this.baselineStats = bstats;
          this.incomingStats = istats;
          if (res.data.featureWiseDistribution && res.data.featureWiseDistribution.data.length) {
            this.featureWiseDistributionPlot = res.data.featureWiseDistribution;
            this.featureWiseDistributionPlot.layout.template.layout.plot_bgcolor = 'white';
            this.featureWiseDistributionPlot.layout.xaxis.showgrid = false;
            this.featureWiseDistributionPlot.layout.yaxis.showgrid = false;
            this.featureWiseDistributionPlot.layout.yaxis.showline = true;
            this.featureWiseDistributionPlot.layout.yaxis.showline = true;
            this.featureWiseDistributionPlot.layout.xaxis.linecolor = 'black';
            this.featureWiseDistributionPlot.layout.yaxis.linecolor = 'black';
            Plotly.plot(
              graphDiv,
              this.featureWiseDistributionPlot.data,
              this.featureWiseDistributionPlot.layout
            );
          }
          this._toaster.success('Featurewise drift analysis completed', 'Drift Report');
        }
        this._spinner.stopSpinner();
      },
      (err) => {
        this._toaster.error('Socket Error', 'Drift Report');
      }
    );
  }

  /**
   * Unsubscribes the subscriptions after destroy
   */
  ngOnDestroy() {
    this._socket.removeListener('dataDrift');
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
