import { SpinnerService } from 'src/app/shared/spinner.service';
import { Component, Inject, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { NotebookComponent } from '../../notebook/notebook.component';
import { DataDriftService } from '../data-drift.service';

@Component({
  selector: 'app-drift-dialog',
  templateUrl: './drift-dialog.component.html',
  styleUrls: ['./drift-dialog.component.scss']
})
export class DriftDialogComponent implements OnInit {

  /** Form group instance */
  settingsForm: FormGroup;

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  /** Constant arrays */
  algoTypeArray: Array<any> = [{ name: 'Classification', val: 'classification' }, { name: 'Regression', val: 'regression' }];
  classificationMetricArray: Array<any> = [{ name: "Accurracy", value: "accuracy" }, { name: "F1 score", value: "f1_score" }];
  regressionMetricArray: Array<any> = [{ name: "RMSE", value: 'rmse' }, { name: "MAE", value: 'mae' }, { name: "R2", value: 'r2' }];
  metricArray: Array<any> = [{ name: "Population Stability Index (PSI)", value: "psi" }, { name: "Standard Deviation", value: "std" }];
  psiThresholdArray: Array<any> = [{ name: "Less than 0.1 (No change in population)", value: "<0.1" }, { name: "0.1 – 0.20 (Some minor change in population)", value: "0.1-0.20" }, { name: "Greater than 0.20 (Significant change in population)", value: ">0.20" }];
  stdThresholdArray: Array<any> = [{ name: "1 STD (covers 68% of data)", value: '1' }, { name: "2 STD (covers 95% of data)", value: '2' }, { name: "3 STD (covers 99.7% of data)", value: '3' }];

  project: any;

  showSpinner: boolean = false;
  addToPipeline: boolean = false;
  isEdit: boolean = false;
  isDriftType: boolean = false;
  isConceptDriftType: boolean = false;
  isDataDriftType: boolean = false;
  conceptDrift: boolean = false;
  dataDrift: boolean = false;
  modelPerformanceType: boolean = false;
  distributionType: boolean = true;
  numericFeatures: boolean = true;
  categoricalFeatures: boolean = false;
  textFeatures: boolean = false;

  algoType: string = 'classification';
  modelPerformanceMetric: string;
  modelPerformanceThreshold: number;
  distributionMetric: string;
  distributionThreshold: string;
  numericFeaturesMetric: string;
  categoricalFeaturesMetric: string;
  textFeaturesMetric: string;
  numericFeaturesThreshold: string;
  categoricalFeaturesThreshold: string;
  distributionDriftThreshold: number = 50;
  numericFeaturesDriftThreshold: number = 50;
  textFeaturesThreshold: string;
  finalMetricArray: Array<any> = [];
  finalThresholdArray: Array<any> = [];
  settingIdToUpdate: string;

  constructor(
    @Inject(MAT_DIALOG_DATA) public dialogData: any,
    private _fb: FormBuilder,
    private _dataDrift: DataDriftService,
    private _toaster: ToastrService,
    private _notebookDialogRef: MatDialogRef<NotebookComponent>,
    private _localStorage: LocalStorageService,
    private _spinner: SpinnerService) {
    this.settingsForm = this._fb.group({
      configName: ['', Validators.required],
      modelPerformanceMetric: ['', Validators.nullValidator],
      modelPerformanceThreshold: ['', Validators.nullValidator],
      distributionMetric: ['psi', Validators.nullValidator],
      distributionThreshold: ['>0.20', Validators.nullValidator],
      numericFeaturesMetric: ['psi', Validators.nullValidator],
      numericFeaturesThreshold: ['>0.20', Validators.nullValidator],
      categoricalFeaturesMetric: [{ value: 'psi', disabled: true }, Validators.nullValidator],
      categoricalFeaturesThreshold: ['>0.20', Validators.nullValidator],
      textFeaturesMetric: [{ value: 'psi', disabled: true }, Validators.nullValidator],
      textFeaturesThreshold: ['>0.20', Validators.nullValidator]
    });
    this.prefillConnectionForm(this.dialogData.settingsData);
  }

  ngOnInit(): void {
    this.project = this._localStorage.getItem('project');
  }

  setDriftValidator(driftType) {
    if (driftType == 'conceptDrift') {
      this.settingsForm.get('distributionMetric').setValidators(Validators.required);
      this.settingsForm.get('distributionMetric').updateValueAndValidity();
      this.settingsForm.get('distributionThreshold').setValidators(Validators.required);
      this.settingsForm.get('distributionThreshold').updateValueAndValidity();
    } else if (driftType == 'dataDrift') {
      this.settingsForm.get('numericFeaturesMetric').setValidators(Validators.required);
      this.settingsForm.get('numericFeaturesMetric').updateValueAndValidity();
      this.settingsForm.get('numericFeaturesThreshold').setValidators(Validators.required);
      this.settingsForm.get('numericFeaturesThreshold').updateValueAndValidity();
    }
  }

  /**
   * Prefill the settings form for edit functionality
   * @param settingsData selected drift setting details
   */
  prefillConnectionForm(settingsData: any): void {
    if (settingsData) {
      this.isEdit = true;
      this.settingIdToUpdate = settingsData._id;
      this.algoType = settingsData.algoType;
      this.conceptDrift = settingsData.conceptDrift.isSelected;
      this.dataDrift = settingsData.dataDrift.isSelected;
      this.settingsForm.patchValue({
        configName: settingsData.configName
      });

      if (this.conceptDrift) {
        this.modelPerformanceType = settingsData.conceptDrift.modelPerformance.isSelected;
        this.distributionType = settingsData.conceptDrift.distribution.isSelected;
        this.distributionDriftThreshold = settingsData.conceptDrift.distribution.driftThreshold;
        this.settingsForm.patchValue({
          modelPerformanceMetric: settingsData.conceptDrift.modelPerformance.metric,
          modelPerformanceThreshold: settingsData.conceptDrift.modelPerformance.threshold,
          distributionMetric: settingsData.conceptDrift.distribution.metric,
          distributionThreshold: settingsData.conceptDrift.distribution.threshold
        });
      }

      if (this.dataDrift) {
        this.numericFeatures = settingsData.dataDrift.numericFeatures.isSelected;
        this.categoricalFeatures = settingsData.dataDrift.categoricalFeatures.isSelected;
        this.textFeatures = settingsData.dataDrift.textFeatures.isSelected;
        this.numericFeaturesDriftThreshold = settingsData.dataDrift.numericFeatures.driftThreshold;
        this.settingsForm.patchValue({
          numericFeaturesMetric: settingsData.dataDrift.numericFeatures.metric,
          numericFeaturesThreshold: settingsData.dataDrift.numericFeatures.threshold,
          categoricalFeaturesMetric: settingsData.dataDrift.categoricalFeatures.metric,
          categoricalFeaturesThreshold: settingsData.dataDrift.categoricalFeatures.threshold,
          textFeaturesMetric: settingsData.dataDrift.textFeatures.metric,
          textFeaturesThreshold: settingsData.dataDrift.textFeatures.threshold
        });
      }

      // Handling hint messages
      this.conceptDrift || this.dataDrift ? this.isDriftType = true : this.isDriftType = false;
      this.modelPerformanceType || this.distributionType ? this.isConceptDriftType = true : this.isConceptDriftType = false;
      this.numericFeatures || this.categoricalFeaturesMetric || this.textFeatures
        ? this.isDataDriftType = true
        : this.isDataDriftType = false;
    } else {
      this.settingsForm.reset();
      this.isEdit = false
    }
  }

  /**
   * Handles the on submit click event as per the functionality - create/edit
   */
  onSubmitSettings(formValue: any): void {
    this.showSpinner = true;
    let reqObj = this.createRequestObject(formValue);
    if (this.isEdit) {
      this.updateSettingHandler(reqObj);
    } else {
      this.createNewSettingHandler(reqObj);
    }
  }

  /**
   * Creates the request structure to parse in API call
   * @param formValue 
   */
  createRequestObject(formValue: any): any {
    // Concept drift details
    let conceptDrift: any = {
      isSelected: this.conceptDrift,
      modelPerformance: {
        isSelected: this.modelPerformanceType,
        metric: formValue.modelPerformanceMetric,
        threshold: formValue.modelPerformanceThreshold,
      },
      distribution: {
        isSelected: this.distributionType,
        metric: formValue.distributionMetric,
        threshold: formValue.distributionThreshold === '1' || formValue.distributionThreshold === '2' || formValue.distributionThreshold === '3' ? parseInt(formValue.distributionThreshold) : formValue.distributionThreshold,
        driftThreshold: this.distributionDriftThreshold,
      }
    }

    // Data drift details
    let dataDrift: any = {
      isSelected: this.dataDrift,
      numericFeatures: {
        isSelected: this.numericFeatures,
        metric: formValue.numericFeaturesMetric,
        threshold: formValue.numericFeaturesThreshold === '1' || formValue.numericFeaturesThreshold === '2' || formValue.numericFeaturesThreshold === '3' ? parseInt(formValue.numericFeaturesThreshold) : formValue.numericFeaturesThreshold,
        driftThreshold: this.numericFeaturesDriftThreshold,
      },
      categoricalFeatures: {
        isSelected: this.categoricalFeatures,
        newCategories: true,
        metric: formValue.categoricalFeaturesMetric ? formValue.categoricalFeaturesMetric : this.settingsForm.get('categoricalFeaturesMetric').value,
        threshold: formValue.categoricalFeaturesThreshold,
      },
      textFeatures: {
        isSelected: this.textFeatures,
        metric: formValue.textFeaturesMetric ? formValue.textFeaturesMetric : this.settingsForm.get('textFeaturesMetric').value,
        threshold: formValue.textFeaturesThreshold,
      }
    }

    // Final request object
    return {
      configName: formValue.configName,
      algoType: this.algoType,
      conceptDrift: conceptDrift,
      dataDrift: dataDrift,
      createdBy: this.project.createdBy,
      projectId: this.project._id,
    }
  }

  /**
   * Creates a new drift setting record into DB
   * @param formValue 
   */
  createNewSettingHandler(settingData: any): void {
    this._dataDrift.createNewSetting(this.project._id, settingData)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe((res: any) => {
        this.showSpinner = false;
        if (res) {
          this._toaster.success('Setting created successfully', 'Drift Setting');
          this.closeModal(res);
        } else {
          this._toaster.error('Cannot create new setting', 'Drift Setting');
        }
      }, (err: Error) => {
        this.showSpinner = false;
        this._toaster.error('Server Error', 'Drift Setting');
      });
  }

  /**
   * Updates the setting details in DB
   * @param formValue 
   */
  updateSettingHandler(settingData: any): void {
    this._dataDrift.updateSetting(this.project._id, this.settingIdToUpdate, settingData)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe((res: any) => {
        this.showSpinner = false;
        if (res) {
          this._toaster.success('Setting updated successfully', 'Drift Setting');
          this.closeModal(res);
        } else {
          this._toaster.error('Cannot update setting', 'Drift Setting');
        }
      }, (err: Error) => {
        this.showSpinner = false;
        this._toaster.error('Server Error', 'Drift Setting');
      });
  }

  /**
   * Function is called when algo type is changed
   * @param {*} type classification/regression
   */
  algoTypeChanged(type: string): void {
    this.algoType = type;
    this.conceptDriftChanged();
  }

  /**
   * Function is called when drift type is changed - concept drift/data drift
   * And sets default values as per the selected type
   */
  driftTypeChanged(driftType): void {
    this.setDriftValidator(driftType);
    // Handling hint messages
    this.conceptDrift || this.dataDrift ? this.isDriftType = true : this.isDriftType = false;
    this.modelPerformanceType || this.distributionType ? this.isConceptDriftType = true : this.isConceptDriftType = false;
    this.numericFeatures || this.categoricalFeaturesMetric || this.textFeatures
      ? this.isDataDriftType = true
      : this.isDataDriftType = false;

    if (this.conceptDrift) {
      this.settingsForm.get('distributionMetric').setValue('psi');
      this.settingsForm.get('distributionThreshold').setValue('>0.20');
    }
    if (this.dataDrift) {
      this.numericFeatures = true;
      this.settingsForm.get('numericFeaturesMetric').setValue('psi');
      this.settingsForm.get('numericFeaturesThreshold').setValue('>0.20');
      this.settingsForm.get('categoricalFeaturesMetric').setValue('psi');
      this.settingsForm.get('categoricalFeaturesThreshold').setValue('>0.20');
      this.settingsForm.get('textFeaturesMetric').setValue('psi');
      this.settingsForm.get('textFeaturesThreshold').setValue('>0.20');
    }
  }

  /**
   * Function is called when concept drift type is changed - model performance/distribution
   * And sets default values as per the selected type
   */
  conceptDriftChanged(): void {
    // Handling hint messages
    this.modelPerformanceType || this.distributionType ? this.isConceptDriftType = true : this.isConceptDriftType = false;
    if (this.modelPerformanceType) {
      this.algoType == 'classification'
        ? this.settingsForm.get('modelPerformanceMetric').setValue('f1_score')
        : this.settingsForm.get('modelPerformanceMetric').setValue('rmse');

      this.settingsForm.get('modelPerformanceMetric').value === 'rmse' || this.settingsForm.get('modelPerformanceMetric').value === 'mae'
        ? this.settingsForm.get('modelPerformanceThreshold').setValue(0)
        : this.settingsForm.get('modelPerformanceThreshold').setValue(50);
    }
    if (this.distributionType) {
      this.settingsForm.get('distributionMetric').setValue('psi');
      this.settingsForm.get('distributionThreshold').setValue('>0.20');
    }
  }

  /**
   * Function is called when data drift type is changed - numeric/categorical/text
   * And sets default values as per the selected type
   */
  dataDriftChanged() {
    // Handling hint messages
    this.numericFeatures || this.categoricalFeaturesMetric || this.textFeatures
      ? this.isDataDriftType = true
      : this.isDataDriftType = false;
  }

  /**
   * Function is called when metric is changed from drop-down
   * And sets default values as per the selected type
   */
  metricChanged() {
    this.settingsForm.get('distributionMetric').value === 'psi'
      ? this.settingsForm.get('distributionThreshold').setValue('>0.20')
      : this.settingsForm.get('distributionThreshold').setValue('3');

    this.settingsForm.get('numericFeaturesMetric').value === 'psi'
      ? this.settingsForm.get('numericFeaturesThreshold').setValue('>0.20')
      : this.settingsForm.get('numericFeaturesThreshold').setValue('3');

    this.settingsForm.get('modelPerformanceMetric').value === 'rmse' || this.settingsForm.get('modelPerformanceMetric').value === 'mae'
      ? this.settingsForm.get('modelPerformanceThreshold').setValue(0)
      : this.settingsForm.get('modelPerformanceThreshold').setValue(50);
  }

  /**
   * Appends the % symbol to the slider thumb count
   * @param value 
   * @returns 
   */
  formatLabel(value: number): string {
    return `${value}%`;
  }

  /**
   * Closes the opened modal by sending the data or without data
   */
  closeModal(data?: any): void {
    this._notebookDialogRef.close(data);
  }

  /**
   * Unsubscribes the subscriptions after destroy
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
