import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DriftReportComponent } from './drift-report/drift-report.component';
import { DriftSettingsComponent } from './drift-settings/drift-settings.component';
import { RouterModule, Routes } from '@angular/router';
import { DriftDialogComponent } from './drift-dialog/drift-dialog.component';
import { SharedModule } from 'src/app/shared/shared.module';
import { DriftOverviewComponent } from './drift-overview/drift-overview.component';

const routes: Routes = [
  {
    path: 'report',
    component: DriftReportComponent,
  },
  {
    path: 'settings',
    component: DriftSettingsComponent,
  },
];

@NgModule({
  declarations: [DriftReportComponent, DriftSettingsComponent, DriftDialogComponent, DriftOverviewComponent],
  imports: [
    CommonModule,
    SharedModule,
    RouterModule.forChild(routes),
  ]
})
export class DataDriftModule { }
