import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { DataDriftService } from '../data-drift.service';

const swal = require('sweetalert');
@Component({
  selector: 'app-drift-report',
  templateUrl: './drift-report.component.html',
  styleUrls: ['./drift-report.component.scss'],
})
export class DriftReportComponent implements OnInit {
  /** Data table settings */
  dataSource: MatTableDataSource<[]> = new MatTableDataSource();
  @ViewChild('driftReportPaginator') set paginator(value: MatPaginator) {
    this.dataSource.paginator = value;
  }
  @ViewChild('driftReportSort') set sort(value: MatSort) {
    this.dataSource.sort = value;
  }

  pageSizeOptions: Array<number> = [10, 15, 20];
  displayedColumns: Array<string> = [];

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  showSpinner: boolean = false;
  user: any;
  project: any;
  reportsArray: Array<any>;
  showOverview: boolean = false;
  reportDetails: any;

  constructor(
    private _toaster: ToastrService,
    private _localStorage: LocalStorageService,
    private _spinner: SpinnerService,
    private _dataDrift: DataDriftService
  ) {
    this.project = this._localStorage.getItem('project');
    this.user = this._localStorage.getItem('user');
    this.displayedColumns = ['srNo', 'reportName', 'algoType', 'createdAt', 'actions'];
  }

  ngOnInit(): void {
    this._spinner.showSpinner();
    this.getReportsList();
  }

  /**
   * Fetches all the drift reports list from DB
   */
  getReportsList(): void {
    this._dataDrift
      .getAllReports(this.project._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          this._spinner.stopSpinner();
          if (res) {
            this.reportsArray = res;
            this.dataSource.data = res;
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
            this.dataSource._updateChangeSubscription();
          } else {
            this._toaster.error('Cannot fetch drift reports', 'Drift Reports');
          }
        },
        (err: Error) => {
          this._spinner.stopSpinner();
          this._toaster.error('Server Error', 'Drift Reports');
        }
      );
  }

  /**
   * Fetches the report details from DB to show overview
   * @param reportData
   */
  showReportOverview(reportData: any): void {
    this._spinner.showSpinner();
    this._dataDrift
      .getReportDetails(this.project._id, reportData._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          this._spinner.stopSpinner();
          if (res) {
            this.reportDetails = res;
            this.showOverview = true;
          } else {
            this._toaster.error('Cannot fetch report overview', 'Drift Reports');
          }
        },
        (err: Error) => {
          this._spinner.stopSpinner();
          this._toaster.error('Server Error', 'Drift Reports');
        }
      );
  }

  /**
   * Handles the delete operation to delete report
   * @param reportData
   */
  deleteHandler(reportData: any): void {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this drift report!',
      icon: 'warning',
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
        },
        confirm: {
          text: 'Yes, delete it!',
          value: true,
          visible: true,
          className: 'bg-danger',
        },
      },
    }).then((isConfirm: boolean) => {
      if (isConfirm) {
        this.showSpinner = true;
        this._dataDrift.deleteReport(this.project._id, reportData._id).subscribe(
          (res: any) => {
            if (res) {
              this.reportsArray = this.reportsArray.filter((item) => item._id !== reportData._id);
              this.dataSource.data = this.reportsArray;
              this.dataSource._updateChangeSubscription();
              this._toaster.success(
                `${reportData.reportName} deleted successfully.`,
                'Drift Reports'
              );
            } else {
              this._toaster.error('Cannot delete the drift report', 'Drift Reports');
            }
            this.showSpinner = false;
          },
          (err: Error) => {
            this.showSpinner = false;
            this._toaster.error('Server Error', 'Drift Reports');
          }
        );
      }
    });
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Unsubscribes the subscriptions after destroy
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
