import { DataDriftService } from './../data-drift.service';
import { DriftDialogComponent } from './../drift-dialog/drift-dialog.component';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { SafeResourceUrl, DomSanitizer } from '@angular/platform-browser';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { SchedularService } from '../../dashboard/schedular/schedular.service';
import { DataService } from '../../data/data.service';
import { takeUntil } from 'rxjs/operators';

const swal = require('sweetalert');

@Component({
  selector: 'app-drift-settings',
  templateUrl: './drift-settings.component.html',
  styleUrls: ['./drift-settings.component.scss'],
})
export class DriftSettingsComponent implements OnInit {
  /** Data table settings */
  dataSource: MatTableDataSource<[]> = new MatTableDataSource();
  @ViewChild('driftSetPaginator') set paginator(value: MatPaginator) {
    this.dataSource.paginator = value;
  }
  @ViewChild('driftSetSort') set sort(value: MatSort) {
    this.dataSource.sort = value;
  }

  pageSizeOptions: Array<number> = [10, 15, 20];
  displayedColumns: Array<string> = [];

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  showSpinner: boolean = false;
  user: any;
  project: any;
  dataGroupList: Array<any>;
  dataSetList: Array<any>;
  safeUrl: SafeResourceUrl;
  settingArray: Array<any>;

  constructor(
    private _toaster: ToastrService,
    private _localStorage: LocalStorageService,
    private _spinner: SpinnerService,
    private _dialog: MatDialog,
    private _sanitizer: DomSanitizer,
    private _dataDrift: DataDriftService,
    private _data: DataService,
    private _schedular: SchedularService
  ) {
    this.project = this._localStorage.getItem('project');
    this.user = this._localStorage.getItem('user');
    this.displayedColumns = ['srNo', 'configName', 'algoType', 'createdAt', 'actions'];
  }

  ngOnInit(): void {
    this._spinner.showSpinner();
    this.getSettingsList();
  }

  /**
   * Fetches all the drift configurations from DB
   */
  getSettingsList(): void {
    this._dataDrift
      .getAllSettings(this.project._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          this._spinner.stopSpinner();
          if (res) {
            this.settingArray = res;
            this.dataSource.data = res;
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
            this.dataSource._updateChangeSubscription();
          } else {
            this._toaster.error('Cannot fetch drift settings', 'Drift Settings');
          }
        },
        (err: Error) => {
          this._spinner.stopSpinner();
          this._toaster.error('Server Error', 'Drift Settings');
        }
      );
  }

  /**
   * Opens the dialog with form
   */
  openFormDialog(settingData?: any): void {
    let dialodRef = this._dialog.open(DriftDialogComponent, {
      minWidth: '50vw',
      data: {
        dataGroups: this.dataGroupList,
        dataSets: this.dataSetList,
        settingsData: settingData,
      },
    });

    dialodRef.afterClosed().subscribe((data) => {
      if (data) {
        this.getSettingsList();
      }
    });
  }

  /**
   * Handles the delete operation to delete setting
   * @param settingData
   */
  deleteHandler(settingData: any): void {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this drift settings!',
      icon: 'warning',
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
        },
        confirm: {
          text: 'Yes, delete it!',
          value: true,
          visible: true,
          className: 'bg-danger',
        },
      },
    }).then((isConfirm: boolean) => {
      if (isConfirm) {
        this.showSpinner = true;
        this._dataDrift.deleteSetting(this.project._id, settingData._id).subscribe(
          (res: any) => {
            if (res) {
              this.settingArray = this.settingArray.filter((item) => item._id !== settingData._id);
              this.dataSource.data = this.settingArray;
              this.dataSource._updateChangeSubscription();
              this._toaster.success(
                `${settingData.fileName} deleted successfully`,
                'Drift Settings'
              );
            } else {
              this._toaster.error('Cannot delete the drift setting', 'Drift Settings');
            }
            this.showSpinner = false;
          },
          (err: Error) => {
            this.showSpinner = false;
            this._toaster.error('Server Error', 'Drift Settings');
          }
        );
      }
    });
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Unsubscribes the subscriptions after destroy
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
