import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class EdaService {
  serverBaseUrl: string = environment.serverBaseUrl;

  constructor(private _http: HttpClient) { }

  /**
   * Does the multi uni variate analysis and generates plot
   * @param projectId 
   * @param reqObj 
   * @returns 
   */
  generateMultiUniVariatePlot(projectId: string, reqObj: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/v2/projects/${projectId}/report/trainmodel/multiunivariate`, reqObj);
  }

  /**
   * Shares the report
   * @param projectId 
   * @param reqObj 
   * @returns 
   */
  shareReport(projectId: string, reqObj: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/v2/projects/${projectId}/output`, reqObj);
  }

  /**
   * Starts the EDA execution process
   * @param projectId 
   * @param reqObj 
   * @returns 
   */
  startEda(projectId: string, reqObj: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/v2/projects/${projectId}/eda`, reqObj);
  }

  /**
   * Get the details about a specific EDA
   * @param edaId 
   * @returns 
   */
  getEdaData(edaId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/v2/eda/${edaId}/info`);
  }

  /**
   * Reads the uploaded EDA data (summary/features)
   * @param projectId 
   * @param reqObj 
   * @returns 
   */
  readUploadedData(projectId: string, reqObj: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/projects/${projectId}/data/read`, reqObj);
  }

  /**
   * Reads Class for the categorical feature
   * @param reqObj 
   * @returns 
   */
  getUniqueClass(reqObj: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/v2/training/feature/unique_categories`, reqObj);
  }

  /**
   * Reads conditional filtering data
   * @param reqObj 
   * @returns 
   */
  getConditionalFilter(reqObj: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/v2/conditionalFiltering`, reqObj);
  }

  /**
   * Gets List of target
   * @param reqObj 
   * @returns 
   */
  getTargetList(reqObj: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/v2/eda/targetList`, reqObj);
  }

  /**
   * Function to find previous Eda details
   * @param projectId 
   * @param fileId 
   * @returns 
   */
  getEdaByFileId(projectId: string, fileId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/v2/projects/${projectId}/eda/edaByFile?fileId=${fileId}`);
  }

  /**
   * Function to validate the date format
   * @param reqObj 
   * @returns 
   */
  dateFormatValidator(reqObj: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/v2/eda/dateFormat`, reqObj, { responseType: 'text' });
  }

  multiTimeseriesCheck(reqObj: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/v2/eda/multiTimeseries`, reqObj, { responseType: 'text' });
  }

  /**
   * Reads conditional filtering data
   * @param reqObj 
   * @returns 
   */
  getresamplingPreview(reqObj: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/v2/resamplingPreview`, reqObj);
  }

  /**
   * Reads conditional filtering data
   * @param reqObj 
   * @returns 
   */
  calculateTimeseriesFrequency(reqObj: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/v2/multiple/calculateFrequency`, reqObj);
  }

  /**
   * find timeseriesGroup
   */
  getTimeseriesGroup(reqObj: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/v2/eda/timeseriesGroupList`, reqObj);
  }
}
